// src/components/PrivacyPolicy.js

import React from 'react';
import { useNavigate } from 'react-router-dom';

const PrivacyPolicy = () => {
  const navigate = useNavigate();

  const handleAceitar = () => {
    // Aqui voc pode chamar a funo de consentimento para registrar que o usurio aceitou
    localStorage.setItem('userConsent', 'true');
    navigate('/'); // Redireciona o usurio para a pgina inicial ou para onde desejar
  };

  return (
    <div style={styles.container}>
      <h2>Poltica de Privacidade  Max Fibra</h2>
      <p>A Max Fibra, provedor de internet da regio de Viana, est comprometida em proteger a privacidade dos usurios do aplicativo. Esta Poltica de Privacidade descreve como coletamos, usamos, armazenamos e protegemos as informaes pessoais dos usurios que acessam o aplicativo da Max Fibra para gerenciar seus dados, incluindo informaes financeiras de faturas em aberto e alteraes de senha de Wi-Fi.</p>
      <h3>Coleta de Informaes</h3>
      <p>
        Ao utilizar o aplicativo da Max Fibra, podemos coletar as seguintes informaes:
        <ul>
          <li><strong>Dados Pessoais</strong>: Informaes como nome, endereo, nmero de telefone, e-mail e CPF, que so necessrias para identificar e fornecer servios aos usurios.</li>
          <li><strong>Dados Financeiros</strong>: Informaes relacionadas s faturas em aberto, incluindo valores, datas de vencimento e histrico de pagamentos.</li>
          <li><strong>Dados de Conexo</strong>: Informaes relacionadas ao servio de internet, como o status da conexo, endereo IP e informaes do roteador para a mudana de senha de Wi-Fi.</li>
          <li><strong>Dados de Uso</strong>: Informaes sobre como voc utiliza o aplicativo, como pginas visitadas, tempo de uso e interaes com os recursos do aplicativo.</li>
          <li><strong>Dados de Imagem</strong>: O aplicativo pode acessar imagens armazenadas no dispositivo para fins de personalizao ou funcionalidade do usurio. No entanto, nenhuma imagem  carregada ou enviada para servidores externos sem consentimento explcito do usurio.</li>
        </ul>
      </p>
      <h3>Uso das Informaes</h3>
      <p>As informaes coletadas so utilizadas para:</p>
      <ul>
        <li>Fornecer e melhorar os servios oferecidos pelo aplicativo.</li>
        <li>Perguntar aos usurios sobre suas faturas em aberto e gerenciar suas contas.</li>
        <li>Facilitar a alterao de configuraes do servio, como mudana de senha do Wi-Fi.</li>
        <li>Enviar notificaes e atualizaes relacionadas ao servio e  conta do usurio.</li>
        <li>Cumprir obrigaes legais e regulatrias.</li>
      </ul>
      <h3>Segurana das Informaes</h3>
      <p>A Max Fibra implementa medidas de segurana adequadas para proteger as informaes dos usurios contra acesso no autorizado, alterao, divulgao ou destruio...</p>
      <h3>Alteraes na Poltica de Privacidade</h3>
      <p>A Max Fibra pode atualizar esta Poltica de Privacidade periodicamente. Os usurios sero notificados de quaisquer mudanas significativas...</p>
      <h3>Contato</h3>
      <p>Se voc tiver dvidas ou preocupaes sobre esta Poltica de Privacidade ou sobre o uso de suas informaes pessoais, entre em contato conosco atravs do e-mail: <a href="mailto:contato@grupomaxltda.com.br">contato@grupomaxltda.com.br</a></p>
    </div>
  );
};

const styles = {
  container: {
    padding: '20px',
    maxWidth: '800px',
    margin: '0 auto',
    fontFamily: 'Arial, sans-serif',
    lineHeight: '1.6',
  },
  buttons: {
    marginTop: '20px',
  },
  button: {
    padding: '10px 20px',
    backgroundColor: '#0ecf00',
    color: '#fff',
    border: 'none',
    borderRadius: '5px',
    cursor: 'pointer',
  },
};

export default PrivacyPolicy;
